    .nav {
        width: 100%;
        max-height: 128px;
        height: 20vw;
        display: flex;
        justify-content: space-between;
    }
    
    .logo {
        font-size: 10.2vw;
        transition: ease-out 120ms;
    }
    
    .logo .small {
        font-size: 8.8vw;
    }
    
    .logo .big {
        font-size: 13vw;
        letter-spacing: 2vw;
        font-weight: 600;
    }
    
    .logo-chita {
        width: 74vw;
    }
    
    .splash-div {
        margin-top: 12vh;
    }
    
    table {
        width: 100%;
    }
    
    table td {
        font-size: 24px;
    }
    
    table .table-button {
        width: 10vw;
        max-width: 64px;
    }
    
    input {
        width: 100%;
        background-color: gray;
        padding: 16px 0;
        border: none;
        border-radius: 4px;
    }
    
    .input-grid {
        margin: 16px 32px;
        display: grid;
        grid-template-columns: auto auto auto auto auto;
        grid-gap: 16px;
    }
    
    .input-grid-item {
        background-color: rgba(255, 255, 255, 0.2);
    }
    
    .input-two-grid {
        margin: 16px 32px;
        display: grid;
        grid-template-columns: auto 5% auto;
        grid-gap: 16px;
    }
    
    .big-description {
        font-size: 54px;
        font-weight: 600;
    }
    
    .tab {
        background-color: white;
        font-size: 5vw;
        color: black;
        padding: 4px 4vw;
        margin: 0 2vw;
        border-radius: 4px;
    }
    
    .nav-button {
        cursor: pointer;
        max-width: 128px;
        max-height: 128px;
        height: 20vw;
    }
    
    .current-set {
        font-size: 60vh;
        /* line-height: 70vh; */
    }
    
    .footer-links {
        bottom: 0;
        width: 100%;
        height: 20vw;
        max-height: 128px;
        display: flex;
        justify-content: space-around;
        position: fixed;
    }
    
    @media only screen and (max-width: 600px) {
        .big-description {
            font-size: 8vw;
        }
    }
    
    @media only screen and (min-width: 600px) {
        .tab {
            font-size: 32px;
        }
        .logo {
            font-size: 70px;
        }
        .logo .small {
            font-size: 60px;
        }
        .logo .big {
            font-size: 84px;
            letter-spacing: 16px;
        }
        .logo-chita {
            width: 480px;
        }
        .splash-div {
            margin-top: 5vh;
        }
    }
    
    @media only screen and (max-height: 600px) {
        .footer-links {
            display: none;
        }
    }